import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    slideStatus: true,
    user_info: null
  },
  mutations: {
    changeSlideStatus (state, bool) {
      state.slideStatus = bool
    },
    changeUserInfo (state, data) {
      state.user_info = data
    }
  },
  actions: {
    CHANGESLIDESTATUS ({ commit }, bool) {
      commit('changeSlideStatus', bool)
    },
    CHANGEUSERINFO ({ commit }, data) {
      commit('changeUserInfo', data)
    }
  },
  modules: {
  }
})
