<template>
  <div>
    <div class="Sub_Title">
      {{title || _title}}
    </div>
    <div class="Border_Top_Box">
      <spanText>{{subtitle || _title}}</spanText>
      <slot />
    </div>
  </div>
</template>
<script>
export default {
  props: {
    title: {
      type: String
    },
    subtitle: {
      type: String
    }
  },
  computed: {
    _title () {
      return this.$route.meta.title
    }
  }
}
</script>