import Vue from 'vue'
import VueRouter from 'vue-router'
// import Home from '../views/Home.vue'

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'Home',
    component: () => import('../layout/Main'),
    children: [
      {
        path: '',
        redirect: 'index'
      },
      {
        path: 'index',
        name: 'HomeIndex',
        component: () => import('../views/Home/Index/index')
      },
      {
        path: 'about',
        name: 'about',
        component: () => import('../views/Home/About/index'),
        meta: {
          title: 'About'
        }
      },
      {
        path: 'service',
        name: 'service',
        component: () => import('../views/Home/Service/index'),
        meta: {
          title: 'Service'
        }
      },
      {
        path: 'disciaimer',
        name: 'disciaimer',
        component: () => import('../views/Home/Disciaimer/index'),
        meta: {
          title: 'Disciaimer'
        }
      },
      {
        path: 'privacypolicy',
        name: 'privacypolicy',
        component: () => import('../views/Home/Privacypolicy/index'),
        meta: {
          title: 'Privacypolicy'
        }
      },
    ],
  },
  // {
  //   path: '/about',
  //   name: 'About',
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  // }
  {
    path: '/login',
    name: 'login',
    component: () => import('../views/Login/index')
  },
  {
    path: '/register',
    name: 'register',
    component: () => import('../views/Register/index')
  },
  {
    path: '/forgotpassword',
    name: 'forgotpassword',
    component: () => import('../views/ForgotPassword/index')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
